<template>
  <div class="classifyPage">
      <HomeLink></HomeLink>
      <div class="nav">
      <div class="returnPre" @click="$router.go(-1)">
        <!-- <i class="fa fa-chevron-left" aria-hidden="true"></i> -->
        <span style="font-size:18px"> </span>
         <span> </span>
      </div>
      <div class="title"></div>
      <div class="jump"></div>
    </div>
    <div class="topImg">
      <div>
        <img src="../assets/lunbo1.jpg" />
      </div>
      <div>
        <img src="../assets/lunbo3.jpg" />
      </div>
    </div>

    <template v-if="showIf">
      <van-tabs id="vanTitle" v-model="active">
        <!-- v-for -->
        <van-tab :title="TagBar[index]" v-for="(value, key, index) in clify" :key="index">
          <classifyCardBox 
            :bookList="value"
            :subcategory="subcategory[key]"
            :classTag="key"
            @s-subcategory="$emit('s-subcategory',$event)"
            @s-classTag="$emit('s-classTag',$event)"
          ></classifyCardBox>
        </van-tab>
      </van-tabs>
    </template>
  </div>
</template>

<script>
import Vue from "vue";
import { Tab, Tabs } from "vant";
import classifyCardBox from "@/components/classifyCardBox.vue";
import HomeLink from "@/components/HomeLink.vue";

Vue.use(Tab);
Vue.use(Tabs);

export default {
  components: {
    classifyCardBox,
    HomeLink
  },
  data() {
    return {
      active: 0,
      clify: null,
      showIf: false,
      subcategory: null,
      TagBar:["","","",""]
    };
  },
  methods: {},
  created() {
    // 
    // 
    this.axios
      .get("/categories")
      .then(response => {
        console.log(response);
        this.clify = response.data
        // dataok
        Vue.delete(this.clify,"ok")
      })
      .catch(function(error) {
        console.log(error);
      });

    // 
    // Tab
    this.axios
    .get("/sub-categories")
    .then(response => {
      console.log(response);
      this.subcategory = response.data;
      // 
      this.showIf = true;
    })
    .catch(function(error) {
        console.log(error);
      });
  }
};
</script>

<style lang="less" scoped>
.classifyPage{
  padding-bottom: 50px;
}
.nav {
  display: flex;
  height: 50px;
  align-items: center;
  padding: 0 10px;
  background: #b83320;
  color: white;
  margin-bottom: 1px;
  .returnPre{
    font-size: 14px;
  }
  & > div {
    flex: 1;
  }
  .title {
    flex: 2;
    text-align: center;
  }
}
.topImg {
  display: flex;
  padding: 10px;
  & > div {
    flex: 1;
    // flex-grow: 50%;
    margin: 0 5px;
    img {
      max-width: 100%;
    }
  }
}
/deep/ #vanTitle .van-tab {
  font-size: 18px;
  font-weight: 600;
}
</style>